import "./SearchResults.pcss";

import React from "react";
import SearchStore from "../../SearchStore";

const predictionValueToColor = {
    0: "#EC55EC",
    1: "#aaaaaa",
    2: "#358BCD",
};

const predictionValueToLabel = {
	0: "👎Against",
	1: "😐Neutral",
	2: "👍Favor",
}

const SearchResultsAggregation = function ({ searchState, results }) {
    if (searchState['vertical'] === "text") {
        return (
            <></>
        )
    }
    const topic = SearchStore.getTopic()
    let aggregation = results.map((result) => {
        return (
            <div id="ax"
                style={{
                    ...style.rectangle,
                    backgroundColor: predictionValueToColor[Math.round(result.prediction)],
                }}>
               {predictionValueToLabel[Math.round(result.prediction)]} to {topic ? topic : "<unknown>"} topic
            </div>
        );
    });

    // let sumStance = 0;
    // results.forEach((result) => {
    //     sumStance += Math.round(result.prediction);
    // });

    return (
        <div style={style.Box}>
            <h3 style={{ margin: "5px" }}>Stance Overview of Search Results Appearing in this Page:</h3>
            {aggregation}
            {/* Overall Stance: {sumStance} */}
        </div>
    );
};

const style = {
    Box: {
        // height: "500px",
        border: "1px solid #BDC3C7",
        borderRadius: "5px",
        marginTop: "5px",
        boxShadow: "0px 1px 1px 0px #BFBFBF",
    },
    rectangle: {
        textAlign: "center",
        fontWeight: "bold",
        border: "1px solid #BDC3C3",
        // width: "99%",
        height: "30px",
        fontSize: "12px",
    },
};

export default SearchResultsAggregation;
