import { extendObservable } from "mobx";

// console.log("------ userStore ");
class UserStore {
    constructor() {
        extendObservable(this, {
            loading: true,
            isLoggedIn: false,
            fromProlific: false,
            username: "",
            topic: "",

            getIsLoggedIn() {
                return this.isLoggedIn;
            },
            getUsername: function() {
                return this.username;
            },
            setFromProlific(newBool) {
                this.fromProlific = newBool;
            },
            setLoading(newBool) {
                this.loading = newBool;
            },
            setIsLoggedIn(newBool) {
                this.isLoggedIn = newBool;
            },
            setUsername(newUserName) {
                this.username = newUserName;
                localStorage.setItem("username", newUserName);
            },
            setProlificID(pID) {
                this.ProlificID = pID;
                localStorage.setItem("prolificid", pID);
            },
            getProlificID() {
                return this.ProlificID;
            },
            setProlificSessionID(pSID) {
                this.ProlificSessionID = pSID;
                localStorage.setItem("psessionid", pSID);
            },
            getProlificSessionID() {
                return this.ProlificSessionID;
            },
            setProlificStudyID(studyID) {
                this.ProlificStudyID = studyID;
                localStorage.setItem("pstudyid", studyID);
            },
            getProlificStudyID() {
                return this.ProlificStudyID;
            },
        });
    }
}

export default new UserStore();
