import "./SearchHeader.pcss";
import React from "react";

import Logo from "./Logo";
import SearchBox from "./SearchBox";
import SearchVerticals from "./SearchVerticals";
import AccountInfo from "./AccountInfo";
import SuggestionsContainer from "../../features/querysuggestions/SuggestionsContainer";

const Header = function({
    query,
    vertical,
    provider,
    searchHandler,
    queryChangeHandler,
    verticalChangeHandler,
    timer,
    statusbar,
    userId,
    groupId,
    showAccountInfo,
    hideSuggestionsHandler,
    showSuggestionsHandler,
    clickSuggestionHandler,
    showSuggestions,
    isManager,
    username,
    sessionId,
    searchTopic,
    searchBias,
    userView,
    showSearchHints,
    ProlificID,
    ProlificSessionID,
    ProlificStudyID,
}) {
    return (
        <div className="SearchHeader">
            <Logo />
            <form
                id="search-form"
                action="/"
                method="GET"
                className="form"
                onSubmit={(e) => {
                    e.preventDefault();
                    searchHandler();
                }}
            >
                <SearchBox
                    query={query}
                    changeHandler={queryChangeHandler}
                    showSuggestionsHandler={showSuggestionsHandler}
                    showSearchHints={showSearchHints}
                />
                <SuggestionsContainer
                    clickSuggestionHandler={clickSuggestionHandler}
                    hideSuggestionsHandler={hideSuggestionsHandler}
                    showSuggestions={showSuggestions}
                />

                <SearchVerticals
                    query={query}
                    activeVertical={vertical}
                    changeHandler={verticalChangeHandler}
                    provider={provider}
                />
            </form>

            {showAccountInfo && (
                <AccountInfo
                    userId={userId}
                    groupId={groupId}
                    username={username}
                    isManager={isManager}
                    sessionId={sessionId}
                    searchTopic={searchTopic}
                    searchBias={searchBias}
                    userView={userView}
                    ProlificID={ProlificID}
                    ProlificSessionID={ProlificSessionID}
                    ProlificStudyID={ProlificStudyID}
                />
            )}

            <div className="StatusBarDiv">{statusbar}</div>

            <div className="TimerDiv">{timer}</div>
        </div>
    );
};

export default Header;
